input[type='radio']:after {
    width: 15px;
    height: 15px;
    border-radius: 15px;
    top: 1px;
    left: 0px;
    position: relative;
    background-color: var(--white);
    content: '';
    display: inline-block;
    visibility: visible;
    border: 2px solid var(--orange);
}

input[type='radio']:checked:after {
    width: 15px;
    height: 15px;
    border-radius: 15px;
    top: 1px;
    left: 1px;
    position: relative;
    background-color: #ffc800;
    content: '';
    display: inline-block;
    visibility: visible;
    border: 2px solid  #ff8c00;
}
