:root {
    --orange: #ddaa48;
    --green: #5b9800;
    --blue: #61baed;
    --black:#000000;
    --white:#ffffff;
    --font:  sans-serif;
    --green2:#88c58f;
    --blue2:#005dd9;
  }
  
  *, ::after, ::before {
    box-sizing: inherit;
    margin: 0;
    padding: 0;
}
.app .columns{
  margin: 0;
  display: flex;
  flex-wrap: wrap;
  width:100%;

  /* background-color: #23ac2d; */
}
.app_subtitle{
  color: var(--green2);
  font-size: 25px;
  margin-bottom: 9px;
}
.li_title{
  color: var(--orange)
}
.app .backBtn{
  border-radius: 50%;
    font-size: 32px;
    color: #fafafa;
    margin: 0;
    font-weight: bold;
    position: absolute;
    top: -148px;
    left: 18px;
    cursor: pointer;
}
.app .flow_label{
  font-size: 13px;
  font-family: var(--font);
  font-weight: 200;
  color: #464242;
  margin-bottom: .2em !important;
}
.app .flow_input {
  border-radius:1px;
height: 2.9rem;
border-color: darkgray;
max-width: 200px;
width:100%;
}
.app .column{
  padding:0;
}
.app .flow_input:focus {
  border-radius:1px;
  border-color: var(--green2);
  box-shadow: none;
}
.app .control{
  max-width: fit-content;
}
.app .nextBtn{
  background-color: var(--green);
  color: white;
  width: 200px;
  font-size: 20px;
  border: none;
  color: var(--white);
}
.app  .btnxt:after{
  content: ' \2192';
  padding-left: 7px;
  font-size: 20px;
}
.otpContainer {
  margin: 5% auto;
}

.otpInput {
  width: 2.5rem !important;
  height: 2.5rem;
  margin: 0 0.5rem;
  font-size: 1.7rem;
  text-align: center;
  border-radius: 4px;
  border: 1.5px solid var(--orange);
}

/* for text only class  */

.app .control_text{
  width:100%;
}

.app .label_class{
  width: 100%;
  border-radius: 41px;
  height: 54px;
  align-items: center;
  text-align: center;
  padding-top: 13px;
  margin: 15px 0px;
  border: 1px solid var(--blue2);

}
.app .selected_label{
  background-color: var(--blue2);
  color: var(--white);
}