.header_section{
    display:flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: flex-end;
    background: url('../imgs/logo2.png');
    background-repeat: no-repeat;
    background-size: 27%;
    background-position: bottom;
    background-color: var(--blue);
    box-shadow: inset 0px -52px 63px -4px rgba(0, 0, 0, 0.75);

    min-height: 139px;
}
.header_logo{
    width:32px;
}
.header_title{
    color: var(--white);
    font-size: 24px;
    /* font-size: var(--font); */
    padding: 0px 0px 8px 14px;

}