.popup_section .popup_content{
    background-color: var(--white);
    height: auto;
    width: 90%;
    margin: 0;
    padding: 20px 10px;
}
.popup_section .delete{
    background-color: var(--white);
    box-shadow: 0 0 0 2px rgba(0, 0,0,0.16);
    float: right;
}
.popup_section .delete::after{
    width:1.9px
}
.popup_section .delete::before{
    height:1.9px
}
.popup_section .delete::after,.popup_section .delete::before{
    background-color: #000000;
}
.model-input{
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 20px;

}